:root {
  /** Colors **/
  --orange: #ed563b;
  --white: #fff;
  --dark: #232d39;
  --red: #cc0000;
  --orange-light: rgb(255, 240, 235);
  --white-dark: rgba(250,250,250,0.99) !important;
  --white-light: rgba(250,250,250,0.1);
  --secondary: rgba(47,59,72,.08);
  --secondary-dark: rgb(167, 167, 167);

  /** Spacing **/
  --spacing-xs: 1px;
  --spacing-s: 2px;
  --spacing-m: 4px;
  --spacing: 8px;
  --spacing-l: 12px;
  --spacing-2: 16px;
  --spacing-xl: 24px;
  --spacing-3: 32px;
  --spacing-4: 48px;
  --spacing-5: 64px;
  --spacing-6: 96px;
  --spacing-7: 128px;
  --spacing-8: 176px;
  --spacing-9: 256px;

  /** Font Weight **/
  --font-weight-normal: 500;
  --font-weight-bold: 800;
  --font-weight-bolder: 900;

  --grid-columns: 12
}

html, body {
  height: 100%;
