.Dot-1 {
  -webkit-animation: anim 1s linear 0s infinite;
          animation: anim 1s linear 0s infinite;
}

.Dot-2 {
  -webkit-animation: anim 1s linear 0.25s infinite;
          animation: anim 1s linear 0.25s infinite;
